<template>
    <div class="HelpContent">
      <HomePageTop :TopTitle="TopTotle" class="bg_fff"></HomePageTop>
      <div class="HelpContentHome">
        <p class="f34 cl_333">一个账户可以绑定两个开票公司资料吗?</p>
        <span class="cl_666 f28 mt50 mb24">
          每一个捷配自然账号只能绑定一个开票信息，
          如有多个打开开票公司，
          可以在会员中心创建子账号，
          每个子账号可以绑定一个新的公司信息。
        </span>
        <span class="cl_666 f24"> 子账号的具体创建使用教程如下：</span>
        <p class="f28 cl_333 mb24">1、  进入会员中心，点击账户设置→子账号管理</p>
        <div style="width: 100%;height: 12.2rem;background: #D8D8D8"></div>
        <p class="f28 cl_333 mt32 mb32">1、  进入会员中心，点击账户设置→子账号管理</p>
        <div style="width: 100%;height: 12.2rem;background: #D8D8D8"></div>
      </div>
      <div class="solve both">
        <span class="fl f24">您的问题是否得到了解决？</span>
        <div>
          <span @click="confirm">是</span>
          <p>否</p>
        </div>
      </div>
    </div>
</template>
<script>
    import Vue from "vue"
    import { Toast } from 'vant';
    import HomePageTop from "../../common/HomePageTop";
    export default {
        name: "HelpContent",
        components: {HomePageTop},
        data() {
            return {
              //像子组件传值
              TopTotle: { name: "认证中心",left: true},
              shangchuan:"",
              texts:"",
              Home:"",
            }
        },
        methods: {
          confirm(){
            let _this=this
            Toast({
              message: '提交成功\n感谢您对捷配的关注',
              icon: require('../../../assets/img/common/succeed_f90.png'),
              onClose:function(){
                _this.$router.push({
                  path:"HelpCenter"
                })
              },
            });
          },
        },
        created() {

        }
    }
</script>

<style lang="less" >
  .HomePageTop .Head{background: #fff;}
  .HelpContentHome{
    padding: 0 .3rem;
    background: #fff;
  }
  .solve{
    width: 100%;
    height: 1.12rem;
    position: fixed;
    bottom: 0;
    box-shadow: 0px -3px 5px 0px rgba(0, 0, 0, 0.12);
    line-height: .66rem;
    padding: .23rem .3rem;
    background: #ffffff;
    div{
      float: right;
      p{
        width: 1.62rem;
        height: .66rem;
        line-height: .66rem;
        border: 1px solid #f90;
        text-align: center;
        color: #f90;
        float: right;
        font-size: .28rem;
        margin-right: .24rem;

      }
      span{
        float: right;
        width: 1.62rem;

        display: inline-block;
        background: #f90;
        color: #fff;
        font-size: .28rem;
        text-align: center;
        line-height: .66rem;


      }
    }

  }
  .van-toast{
    width: 3.2rem;
    height: 2.68rem;
  }
</style>



